import { clamp } from 'gsap/gsap-core';
import * as THREE from 'three'
import { NormalBlending } from 'three';
import * as script from '../script.js';

var mesh, clock, cubeSineDriver,
    smokeMaterial, smokeGeo, smokeParticles, delta;


let galaxyColors = [
    new THREE.Color("#d1ccba").multiplyScalar(2.5),
    new THREE.Color("#a6a49f").multiplyScalar(2.5),
    new THREE.Color("#9c8c64").multiplyScalar(2.5),
    new THREE.Color("#857b63").multiplyScalar(2.5)
];

export function init(scene) {
    clock = new THREE.Clock();



    smokeGeo = new THREE.PlaneGeometry(3, 3);
    smokeParticles = [];
    smokeMaterial = new THREE.MeshLambertMaterial
        ({
            color: galaxyColors[Math.floor(Math.random() * galaxyColors.length)],
            map: new THREE.TextureLoader().
                load("https://iili.io/bzbRnt.png"),
            opacity: 0.2,
            blending: NormalBlending,
            depthWrite: false,
            depthTest: true,
            transparent: true,
            fog: true
        });

    for (var p = 0; p < 100; p++) {


        var particle = new THREE.Mesh(smokeGeo, smokeMaterial);
        var radius = 2;
        particle.position.set(Math.random() * radius - radius / 2, Math.random() * radius + radius / 5, Math.random() * radius - radius);
        particle.rotation.z = Math.random() * 360;
        var randomscale = Math.random() * 2 + .5;
        //particle.scale.set(randomscale, randomscale, randomscale);
        scene.add(particle);
        smokeParticles.push(particle);
    }
    animate();

}

function animate() {
    delta = clock.getDelta();
    requestAnimationFrame(animate);
    evolveSmoke();
}


function evolveSmoke() {
    var sp = smokeParticles.length;
    while (sp--) {
        smokeParticles[sp].rotation.z += (delta * 0.1);
    }
    smokeMaterial.opacity =
        clamp(0, 1, 0.20 - script.bodyScrollBar.offset.y / 5000);
}



